@import url(https://fonts.googleapis.com/css?family=Poiret+One);

@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

:root {
 
  --rad: 0.7rem;
  --dur: 0.3s;
  --color-dark: #bba7a7ee;
  --color-light: #fff;
  --color-brand: #6bbd579f;
  --font-fam: "Lato", sans-serif;
  --height: 5rem;
  --btn-width: 6rem;
  --bez: cubic-bezier(0, 0, 0.43, 1.49);
}

body {
  background-color: #fcc5c0;
  /* background: var(--color-dark); */
  height: 130vh;
  /* min-height: 110vh; */
  display: grid;
  place-items: center;
}

.widget {
  display: flex;
  height: 20rem;
  width: 650px;
  flex-wrap: wrap;
  cursor: pointer;
  border-radius: 20px;
  box-shadow: 0 27px 55px 0 rgba(0, 0, 0, 0.3),
    0 17px 17px 0 rgba(0, 0, 0, 0.15);
  margin: 0rem;
  padding: 0%;
}
.widget .weatherIcon {
  flex: 1 100%;
  height: 60%;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  background: #fafafa;
  font-family: weathericons;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 100px;
  margin: 0%;
}

.widget .weatherInfo {
  flex: 70%;
  height: 40%;
  background: #c689c6;
  display: flex;
  align-items: center;
  color: white;
  margin: 0%;
  padding: 1%;
}
.widget .weatherInfo .temperature {
  flex: 30%;
  flex-direction: column;
  /* width: 100%; */
  /* font-size: 65px; */
  display: flex;
  justify-content: space-around;
  margin-right: 20px;
}
pre {
  font-size: 15px;
  font-family: "Lato", sans-serif;
}
.currTemp {
  font-size: 65px;
}
.widget .weatherInfo .description {
  flex: 30%;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: center;
}
.widget .weatherInfo .description .weatherCondition {
  text-transform: uppercase;
  font-size: 35px;
  font-weight: 100;
}
.widget .weatherInfo .description .place {
  font-size: 15px;
}
.widget .date {
  flex: 25%;
  height: 40%;
  background: #937dc2;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: white;
  font-size: 30px;
  font-weight: 800;
  text-align: center;
  border-bottom-left-radius: 20px;
}

.extra-temp {
  display: flex;
  flex-direction: column;
  padding: 3% 0;
  /* width: 15rem; */
  border-radius: 20px;
  border-radius: 20px;
  background: #fafafa;
  /* display: grid; */
  /* grid-template-columns: repeat(2, 1fr); */
  align-items: center;
  font-size: 100px;
  margin: 0;
}

.extra-temp p {
  font-size: 25px;
  font-weight: 600;
}

.temp-info-minmax,
.weather-extra-info,
.two-sided-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
  align-items: center;
  color: #70c1b3;
  /* background-color: blanchedalmond; */
}

.extra-temp .extra-info-leftside {
  text-align: left;
  font-weight: 600;
  text-transform: capitalize;
  color: #080705;
  font-size: 15px;
}

/* input search button style  */

.search {
  align-items: flex-start;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
}

.searchTerm {
  width: 50%;
  /* border: 3px solid #00b4cc; */
  border: none;
  padding: 16px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  font-size: 18px;
}

.searchButton {
  width: 80px;
  height: 32px;
  border: none;
  background: #937dc2;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
  outline: none;
  font-size: 16px;
}

.searchButton:hover {
  background-color: #17b8ce;
}

.wrap {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  text-align: center;
  margin-top: -150px;
  margin-bottom: 50px;
  flex-direction: column;
}

